@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap");

.rightmost {
	width: 15%;
	height: 100%;
	position: fixed;
	margin-top: 5%;
	background-color: #a689e1;
	height: 80vh;
	border-radius: 0 3% 3% 0;
	margin-right: 2%;
	right: 0;
	overflow-y: scroll;
}

.right {
	margin-top: 5%;
	background-color: #d9d9d9;
	width: 66%;
	overflow-y: hidden;
	height: 80vh;
	position: fixed;
	border-radius: 2% 0 0 2%;
	margin-left: 17%;
}

.column {
	float: left;
}

.left {
	width: 17%;
	height: 100%;
	overflow-y: hidden;
	position: fixed;
	left: 0;
}

.leftbuttonraunak {
	width: 100%;
}

.innerchat {
	overflow-y: auto;
	overflow-x: hidden;
	max-height: 75vh;
}

.outinput {
	position: fixed;
	bottom: 0;
	width: 100%;
	box-sizing: border-box;
	margin-bottom: 3vh;
}

.roomname {
	font-family: "Montserrat", sans-serif;
	text-align: center;
	font-size: 1.5rem;
	font-weight: bold;
}

.roomname2 {
	font-family: "Montserrat", sans-serif;
	text-align: center;
	font-size: 1.7rem;
	font-weight: bold;
}

.gotohash {
	display: flex;
	flex-direction: row;
}

.notifications {
	color: black;
	font-size: 1.2rem;
	display: block;
	padding: 0 0 0 0;
	font-family: "Poppins", sans-serif;
	font-weight: bold;
}

.notifications1 {
	color: black;
	font-size: 1.5rem;
	display: block;
	width: 100%;
	padding: 0 0 0 0;
	margin-top: 5vh;
	font-weight: bold;
	margin-left: 3%;
}

.notifications2 {
	color: #fff;
	font-size: 1.7rem;
	display: block;
	width: 100%;
	padding: 0 0 0 0;
}

.smallcardleft {
	display: block;
	margin-top: 3vh;
	margin-bottom: 3vh;
}

.leftbutton {
	border: none;
	display: block;
	background-color: transparent;
	cursor: pointer;
	margin-bottom: 13%;
}

.chatMessages {
	list-style-type: none;
	padding: 0;
	margin: 2%;
	/* margin-bottom: 3%; */
}

.chatMessage {
	display: flex;
	margin-bottom: 1vh;
	width: 40%;
}

.chatMessage:last-of-type {
	display: flex;
	margin-bottom: 7vh;
	width: 40%;
}

.tooltip1 {
	max-width: 100%;
	padding: 0.75rem;
	border-radius: 0.5rem;
	position: relative;
	margin-right: 1.5%;
}

.tooltip1:before {
	content: "";
	position: absolute;
	bottom: 50%;
	margin-bottom: -0.5rem;
	border: solid transparent;
	border-width: 0.5rem;
}

.tooltip2 {
	max-width: 100%;
	padding: 0.75rem;
	border-radius: 0.5rem;
	position: relative;
	margin-left: 1.5%;
}

.tooltip2:before {
	content: "";
	position: absolute;
	bottom: 50%;
	margin-bottom: -0.5rem;
	border: solid transparent;
	border-width: 0.5rem;
}

.message {
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-weight: 400;
	word-break: break-all;
	width: 100%;
}

.role {
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-style: italic;
	color: #888;
}

.time {
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-size: 0.75rem;
	color: #aaa;
	text-align: right;
}

.time2 {
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-size: 0.75rem;
	color: #aaa;
	text-align: left;
}

.flextime {
	margin-top: 0.1rem;
	display: flex;
	justify-content: space-between;
}

.date {
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-size: 0.75rem;
	color: #aaa;
}

.toparea {
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-size: 0.75rem;
	color: black;
}

.chathead {
	display: flex;
	justify-content: space-between;
}

.chatMessage .tooltip1 {
	align-self: flex-end;
	right: 0;
	width: 100%;
}

.chatMessage .tooltip1:before {
	bottom: 50%;
	left: auto;
	right: -1rem;
}

.chatMessage .tooltip2 {
	align-self: flex-start;
	left: 0;
	width: 100%;
}

.chatMessage .tooltip2:before {
	bottom: 50%;
	right: auto;
	left: -1rem;
}

.chatForm {
	display: flex;
	margin-top: 1rem;
}

.chatButton {
	margin-left: 0.75rem;
	margin-top: 2rem;
	border: none;
	border-radius: 50%;
	color: #fff;
	font-size: 1rem;
	width: 2.5rem;
	height: 2.5rem;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}

.chatInput {
	margin-left: 30%;
	background-color: #f0f0f0;
	color: #333;
	font-size: 1.125rem;
	border-radius: 1.25rem;
	border: none;
	outline: none;
	flex-grow: 0.75;
	margin-top: 2.5rem;
	padding-left: 1.5%;
	padding-right: 1.5%;
}

.chatInput::placeholder {
	color: #aaa;
	padding-left: 5%;
}

.chatButton {
	margin-left: 0.75rem;
	border: none;
	color: #fff;
	font-size: 1rem;
	width: 2.5rem;
	height: 2.5rem;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	cursor: pointer;
}

.notifstyle {
	color: red;
}

::-webkit-scrollbar {
	width: 0.4375rem;
}

::-webkit-scrollbar-thumb {
	background-color: #9e3ffd;
	border-radius: 0.3125rem;
}

.imgstyle {
	float: left;
	margin-top: 1.3rem;
	height: 7rem;
	width: 100%;
	object-fit: cover;
	margin-bottom: 15%;
}

.smallcardleftnew {
	display: block;
	margin-top: 3vh;
	margin-bottom: 3vh;
	margin-left: 2vw;
}

.leftbuttonnew {
	border: none;
	background-color: white;
	display: block;
	margin-top: 20%;
	cursor: pointer;
}

.leftbuttonnew:hover {
	color: #9e3ffd;
}

.notificationsnew {
	color: #505887;
	font-size: 1.2rem;
	display: block;
}

.notificationsnew:hover {
	color: #9e3ffd;
}

.logoutbtn {
	border: white;
	border-radius: 1rem;
	background: white;
	bottom: 0;
	margin-left: 2vw;
	margin-top: 12%;
	cursor: pointer;
}

.welcometext {
	color: #9a9a9a;
	font-size: 1rem;
}

.welcometext2 {
	color: rgb(255, 0, 0);
	font-size: 1rem;
	font-weight: bold;
	letter-spacing: 0.1rem;
	font-family: "Poppins", sans-serif;
}

.smallcardleftnew2 {
	display: block;
	margin-top: 27vh;
	margin-bottom: 3vh;
	margin-left: 2vw;
}

/* Go to Hashtag Hover code */
.popup {
	display: none;
	position: absolute;
	margin-top: 4.5%;
	z-index: 1;
	width: 80%;
	background-color: #fefefe;
	border-radius: 0.6rem;
	box-shadow: 0 0.25rem 0.5rem 0 rgba(0, 0, 0, 0.2), 0 0.375rem 1.25rem 0 rgba(0, 0, 0, 0.19);
}
.popuptxt {
	margin: 0.55rem;
}
.hoverup:hover + .popup {
	display: block;
}
.arrow {
	width: 0;
	height: 0;
	position: absolute;
	border-left: 0.6rem solid transparent;
	border-right: 0.6rem solid transparent;
	border-bottom: 0.6rem solid #c7c7c7;
	margin-top: -4.4%;
	margin-left: 7%;
}
